<template>
  <v-container grid-list-xl class="choose-design-category-component choose-design-component">

    <v-layout align-content-space-between column fill-height>
      <v-layout mt-4 row fill-height :wrap="$vuetify.breakpoint.smAndDown">
        <v-flex>
          <v-btn flat @click="goBack" absolute class="mt-2 hidden-sm-and-down">< Back</v-btn>
          <v-btn flat block @click="goBack" class="mt-2 hidden-md-and-up">< Back</v-btn>
        </v-flex>
        <v-flex xs12>
          <h3 class="display-2 mb-4"> Choose a Category</h3>
        </v-flex>
      </v-layout>

      <v-layout :justify-center="$vuetify.breakpoint.xs" :justify-space-between="$vuetify.breakpoint.smAndUp" wrap>
        <Preloader v-if="$store.state.designCategories.isLoading" />
        <v-flex xs10 sm4 md3 l3 d-block
          v-else
          v-for="item in designCategories.list" :key="item.slug"
        >
          <v-card
            color="grey lighten-3"
            class="category-card"
          >
            <v-img
              :src="item.image"
              @click="selectCategory(item)"
              aspect-ratio="1"
            >
              <v-layout row fill-height align-center>
                <v-flex>
                  <span class="card-text text-uppercase headline text-center font-weight-bold">{{ item.name }}</span>
                </v-flex>
              </v-layout>
            </v-img>
          </v-card>
        </v-flex>
      </v-layout>

      <v-spacer></v-spacer>
    </v-layout>
  </v-container>
</template>

<script>
import Preloader from "../Preloader";

export default {
  name: 'ChooseDesignCategory',
  components: { Preloader },
  data() {
    return {};
  },
  methods: {
    selectCategory(item) {
      this.$store.commit('setDesignCategory', item);
      this.$emit('select', 'chooseDesignSubcategory');
    },
    goBack() {
      this.$emit('select', 'forWho');
    }
  },
  computed: {
    designCategories() {
      return this.$store.state.designCategories;
    }
  },
};
</script>

<style lang="scss">
  // common component for design category and subcategory
  .choose-design-component {
    .category-card {
      cursor: pointer;
      .v-responsive__content>.layout {
        margin-top: 0 !important;
        margin-bottom: 0 !important;
      }
      .v-image__image {
        filter: brightness(75%);
        transition: filter 0.3s linear;
      }
      &:hover {
        .v-image__image {
          filter: brightness(100%);
        }
      }
    }
    .card-text {
      color: #ffffff;
      text-shadow: 0 0 2px #000;
    }
  }
